import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { KtDialogService, SubheaderService } from "../../../../core/_base/layout";
import { environment } from "../../../../../../src/environments/environment";
import { OfflineOrdersService } from "../../../../core/offlineOrders/offlineOrders.service";

interface DataStatisticModel {
	unit: {
		total: number;
		bast: number;
		available: number;
	};
	ticket: {
		total: number;
		open: {
			total: number;
			percentage: number;
		};
		progress: {
			total: number;
			percentage: number;
		};
		done: {
			total: number;
			percentage: number;
		};
	};
}

@Component({
	selector: "kt-general",
	templateUrl: "./general.component.html",
	styleUrls: ["./general.component.scss"],
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class GeneralComponent implements OnInit, OnDestroy {
	data: any;
	subs: any = [];
	loading: boolean;
	dataOffline: number = 0;
	dataOnline: number = 0;

	constructor(private http: HttpClient, private offlineOrdersService: OfflineOrdersService, private ktDialogueService: KtDialogService, private ref: ChangeDetectorRef, private subheaderService: SubheaderService) {}

	ngOnInit() {
		this.loadData();
		// Set title to page breadCrumbs
		this.subheaderService.setTitle("Dashboard");
	}

	// Module to stop loading
	stopLoading() {
		this.loading = false;
		this.ref.markForCheck();
		this.ktDialogueService.hide();
	}

	loadData() {
		this.loading = false;
		this.data = ["access"];
		this.loadOrderOffline();
	}

	loadOrderOffline(): void {
		this.offlineOrdersService.getListOfflineOrdersAll().subscribe(
			(response) => {
				this.dataOffline = response.totalCount;
				this.ref.markForCheck();
			},
			(error) => {
				console.error("Error fetching data:", error);
			}
		);
	}

	ngOnDestroy() {
		// Unsubscribe all subs event to prevent memory leak
		this.subs.map((item) => {
			item.unsubscribe();
		});
	}
}
